const TabRouterMap = [
  ProviderRouter,
  MoneySystemRouter,
  AgreementRouter,
  MessageRouter,
];
function gLeftNav({ Router }) {
  let leftList = [];
  let leftNavCheckState = [];
  if (Router == ProviderRouter.Router) {
    leftList = Router.map((r) => {
      return { name: r.name };
    });
    leftNavCheckState = Router.map((r) => false);
  } else if (Router == AgreementRouter.Router) {
    leftList = agreements;
    leftNavCheckState = agreements.map((r) => false);
    return {
      leftList,
      leftNavCheckState,
    };
  } else if (Router == MessageRouter.Router) {
    leftList = message;
    leftNavCheckState = message.map((r) => false);
  } else if (Router == MoneySystemRouter.Router) {
    leftList = money;
    leftNavCheckState = money.map((r) => false);
  }
  return {
    leftList,
    leftNavCheckState,
  };
}
export default {
  name: "layout-pro",
  setup() {
    const topList = [
      {
        name: "工作台",
        message: "",
      },
      {
        name: "资管系统",
        message: "开发中...暂时无法访问",
      },
      {
        name: "规则协议",
        message: "",
      },
      {
        name: "公告通知",
        message: "",
      },
    ];
    const tabKeyMap = TabRouterMap.map((r) => r.RouterName);
    const topNavCheckState = ref(topList.map((r) => false));
    const leftList = ref([""]);
    const leftNavCheckState = ref([true]);
    const tabIndex = ref(0);
    const tabPath = [rootPath];
    const originLeftNavWidth = 0;
    const showShadow = ref(false);
    TabRouterMap.slice(1).forEach(({ Router }) => {
      tabPath.push(Router[0].path);
    });
    return {
      setContentStyle: "overflow: auto;",
      leftList,
      leftNavCheckState,
      topNavCheckState,
      topList,
      tabKeyMap,
      tabIndex,
      tabPath,
      originLeftNavWidth,
      showShadow,
    };
  },
  components: {
    Container,
  },
  async created() {
    this.$bus.$on("toggleShadow", () => {
      this.setInvoice();
    });
    this.$bus.$on("changeState", (p) => {
      console.log("bus on changestate: ", p);

      this.changeState(p);
    });
    const index = this.backState("tab", this.topNavCheckState);
    this.originLeftNavWidth =
      document.documentElement.style.getPropertyValue("--leftnav-width");
    this.changeState({ top: true, index });
    let userid = window.localStorage.getItem("userID");
    let noti_res = await request({
      method: "POST",
      url: "/api/e9b849a515a84327b424af7ccdbf2949/mobile/common/v1_0_0/notify/all/active",
      data: {
        is_active: true, //总是true
        platform: 10003, // ALL = 10000, IOS = 10001, MINIAPP = 10002
        recipient_id: userid, // 接受公告user的id
      },
      auth: {
        username:
          "NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4",
        password:
          "NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366",
      },
      headers: {
        "Content-Type": "application/json",
      },
    });
    // console.log(noti_res)
    if (noti_res) {
      let gen_noti = noti_res.details.notify_generals;
      let user_noti = noti_res.details.notify_users;
      gen_noti = gen_noti.map((e) => {
        return {
          keyss: "gen_" + e.id,
          title: e.title,
          date: e.date_created,
          content: e.content.text || "",
          read: false,
        };
      });
      user_noti = user_noti.map((e) => {
        return {
          keyss: "user_" + e.id,
          title: e.title,
          date: e.date_created,
          content: e.content.text || "",
          read: e.status == 12004 ? true : false,
        };
      });
      let meslist = [...gen_noti, ...user_noti];
      this.meslist = meslist;
      let unreads = [];
      meslist.map((e) => {
        if (e.read == false) {
          unreads.push(e);
        }
      });
      this.unreads = unreads;
      if (this.unreads.length && index != 3) {
        this.setCSSVAR("--unreadnews-color", "#93d500");
      } else {
        this.setCSSVAR("--unreadnews-color", "white");
      }
    }
  },
  methods: {
    setInvoice() {
      this.showShadow = !this.showShadow;
      this.showInvoice = !this.showInvoice;
    },
    setCSSVAR(key, value) {
      document.documentElement.style.setProperty(key, value);
    },
    setLeftNavWidth() {
      if (this.tabIndex == 2) {
        this.setCSSVAR("--leftnav-width", "200px");
      } else {
        this.setCSSVAR("--leftnav-width", this.originLeftNavWidth);
      }
    },
    updateLeftTab(index) {
      if (this.tabIndex == 0) {
        // 工作台
        this.$router
          .replace(TabRouterMap[this.tabIndex].Router[index]?.path)
          .then((r) => {
            setQuery({ [this.tabKeyMap[this.tabIndex]]: index });
          });
      } else if (this.tabIndex == 1) {
        this.$bus.$emit("setMoney", { index });
        setQuery({ [this.tabKeyMap[this.tabIndex]]: index });
        // 资管平台
      } else if (this.tabIndex == 2) {
        // 规则协议
        console.log("set agreement ");
        this.$bus.$emit("setAgreement", { path: agreements[index]?.hash });
        setQuery({ [this.tabKeyMap[this.tabIndex]]: index });
        console.log("after query set : route m.params .que");
        // console.log(this.$route)
        // console.log(this.$route.params)
        // 消息通知
      } else if (this.tabIndex == 3) {
        const nextRoute = TabRouterMap[this.tabIndex].Router[index]?.path;
        this.$router.replace(nextRoute).then((r) => {
          setQuery({ [this.tabKeyMap[this.tabIndex]]: index });
        });
        // 原消息通知此处无实际功能
        setQuery({ [this.tabKeyMap[this.tabIndex]]: index });
      }
    },
    setLeftNav(index) {
      const { leftList, leftNavCheckState } = gLeftNav(
        TabRouterMap[this.tabIndex]
      );
      leftNavCheckState[index] = true;
      this.leftList = leftList;
      this.leftNavCheckState = leftNavCheckState;
      this.changeState({ left: true, index });
    },
    backState(queryKey, NavCheckState) {
      const index = +query()[queryKey];
      if (
        typeof index == "number" &&
        !isNaN(index) &&
        index >= 0 &&
        index < NavCheckState.length
      ) {
        NavCheckState.forEach((r, i) => (NavCheckState[i] = false));
        NavCheckState[index] = true;
        return index;
      } else {
        NavCheckState[0] = true;
        return 0;
      }
    },
    async changeState({ top, left, index }) {
      // console.log('ProviderBasicLayoutPro --changeState')
      if (top) {
        // 暂时禁用资管平台
        if (top && index == 1) {
          console.log("资管平台正在开发中");
          return;
        }

        this.topNavCheckState.forEach((r, i) => {
          this.topNavCheckState[i] = false;
        });
        this.topNavCheckState[index] = true;
        this.tabIndex = index;
        const lastLeft = +query()[this.tabKeyMap[this.tabIndex]];
        // console.log('last Left: ',lastLeft)

        // console.log('tab path: ',this.tabPath[this.tabIndex])
        this.$router.replace(this.tabPath[this.tabIndex]).then((r) => {
          setQuery({ tab: index });
          this.setLeftNav(lastLeft || 0);
          this.setLeftNavWidth();
        });
      } else if (left) {
        //检查路由
        // console.log('left changestate: ', ...arguments)
        // console.log(index)

        //供应商录入检查
        let providerList = [
          "/tools/provider/settings/update",
          "/tools/provider/profile/update",
          "/tools/provider/activity/update",
          "/tools/provider/progress/review",
          "/tools/provider/feedback",
          "/tools/provider/order/board",
        ];
        let currentpath = this.$route.path;
        // console.log(currentpath)
        let ifprovider = providerList.some((e) => e == currentpath);
        // console.log('if provider: ',ifprovider)
        if (ifprovider) {
          if (index === 1) {
            // console.log('检查主理人设置路由')
            let response_temp = await request({
              method: "POST",
              url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/provider/insurance/info/get",
              data: {
                provider_id: Number(window.localStorage.getItem("providerID")),
              },
              auth,
              headers,
            });
            // console.log(' before ender 主理人设置: ',response_temp);
            if (!response_temp.details) {
              this.$toast.warn(
                "基本信息未通过审核，无法进入主理人设置",
                0,
                500,
                3000
              );
              return;
            }
            // console.log('通过主理人设置 路由守卫')
          } else if (index === 2) {
            console.log("检查商品发布");
            let res2 = await $getProviderByID({
              user_id: Number(window.localStorage.getItem("userID")),
            });
            // console.log('get provider by userid id:',Number(window.localStorage.getItem("userID")))
            console.log("进入商品发布之前的 主理人设置信息：", res2);
            if (!res2.details || res2.details.name == "尚未设置") {
              this.$toast.warn(
                "主理人设置未通过审核，无法商品发布",
                0,
                500,
                3000
              );
              return;
            }
            console.log("通过商品发布设置 路由守卫");
          }
        }

        this.leftNavCheckState.forEach((r, i) => {
          this.leftNavCheckState[i] = false;
        });
        this.leftNavCheckState[index] = true;
        this.updateLeftTab(index || 0);
      }
    },
    logout() {
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010006");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
      this.$router.replace("/tools/login");
    },
  },
  computed: {
    title() {
      return this.topList[this.tabIndex];
    },
    getAccount() {
      return window.localStorage.getItem("userName");
    },
  },
};
